import {
    Component,
    EventEmitter,
    h,
    Prop,
    Event as StencilEvent,
} from '@stencil/core';

export type BadgeSize = 'lg' | 'sm';

/**
 * @category Utilities
 * @description A chip list component for displaying collections of tags, filters, or selectable items. Supports removal, selection, and interactive chip management.
 *
 * @slot - Placed after the chips & the 'clear all' button
 */
@Component({
    tag: 'at-chip-list',
    shadow: false,
})
export class AtChipList {
    /**
     * Items in the chip list.
     */
    @Prop({ mutable: true })
    chips: string[];

    /**
     * Disables the list of chips. Prevents removing a chip.
     */
    @Prop()
    disabled: boolean;

    /**
     * Disables the list of chips but keeps the text readable. Prevents removing a chip.
     */
    @Prop()
    readonly: boolean;

    /**
     * Shows the 'Clear All' button
     */
    @Prop()
    show_clear_all: boolean = true;

    /**
     * Size of the chips. Determines padding and font-size. For use in input chip lists.
     */
    @Prop()
    size: BadgeSize = 'lg';

    /**
     * Emitted when the 'X' on a chip, or 'Clear All' is clicked.
     */
    @StencilEvent({
        eventName: 'atRemoveChip',
        bubbles: true,
        composed: true,
        cancelable: false,
    })
    atuiRemoveChip: EventEmitter<string[]>;

    keyDownHandler(event: KeyboardEvent, chipsToRemove: string[]) {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            this.removeChipHandler(chipsToRemove);
        }
    }

    removeChipHandler(chipsToRemove: string[]): void {
        this.atuiRemoveChip.emit(chipsToRemove);
        if (chipsToRemove.length > 1) {
            this.chips = [];
        } else {
            this.chips = this.chips.filter((chip) => chip !== chipsToRemove[0]);
        }
    }

    get getChips(): any {
        if (!this.chips) {
            return '';
        }

        return this.chips.map((chip) => (
            <at-badge
                class={`flex items-center gap-4 text-center`}
                rounded={true}
                data-name="chip"
                type={this.disabled ? 'disabled' : 'default'}
                size={this.size}
                label={chip}
            >
                {!this.disabled && !this.readonly && (
                    <i
                        class="material-icons text-icon-md text-light hover:text-med relative flex h-[16px] w-[16px] cursor-pointer items-center justify-center text-center"
                        data-name="chip-remove"
                        tabIndex={0}
                        onClick={() => {
                            this.removeChipHandler([chip]);
                        }}
                        onKeyDown={(event) =>
                            this.keyDownHandler(event, [chip])
                        }
                    >
                        cancel
                    </i>
                )}
            </at-badge>
        ));
    }

    render() {
        return (
            <div class="flex h-full flex-wrap items-center gap-4">
                {this.chips && this.getChips}

                {!this.disabled &&
                    this.chips &&
                    this.chips.length > 1 &&
                    this.show_clear_all && (
                        <at-button
                            size="sm"
                            type="secondaryText"
                            label="Clear All"
                            data-name="clear-all"
                            onAtuiClick={() => {
                                this.removeChipHandler(this.chips);
                            }}
                        ></at-button>
                    )}
                <slot />
            </div>
        );
    }
}
